import React, {PureComponent} from 'react'
import {Form, Button, Row, Col, message, Input, Select} from 'antd'
import style from './style/Form.scss'
import {CsxSelect, CsxMultipleSelect, CsxMultipleSearch, CsxSearch} from 'Form'
import {connect} from 'react-redux'
import {proTypeCatorageList} from 'Mock'
import {getData} from 'util'
import {
  API_PRODUCTS_KEYVALUES,
  API_PRODUCT_GETPRODUCTKEYVALUES,
  API_GET_CREATE_MANUAL_ORDER_SAP_CODE,
  API_GET_CONFIGURE_CONFIGURE_DEVICE_SAP_SUB_CODE,
  API_SHIPINGZONECONFIG_LIST_CUSTOMER,
  API_SHIPINGZONECONFIG_LIST_SUB_CUSTOMER
} from 'api'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
import Debounce from 'lodash-decorators/debounce'
import {debounce} from 'lodash'
// console.log(Debounce, 333)

@connect(
  (state) => ({...state.shipingZoneConfig, userLoation: state.userLoation}),
  mapDispatchToProps
)
class App extends PureComponent {
  constructor() {
    super(...arguments)
    this.state = {
      searchProList: [] //商品列表
    }
  }
  // 改变当前的输入值
  onInputChange(values) {
    const {searchParams, update} = this.props
    update({searchParams: {...searchParams, ...values}})
  }

  //查询
  @Debounce(600)
  query = () => {
    const {handleGetList, searchParams = {}, size} = this.props
    const {categoryCodes = '', productType = ''} = searchParams
    console.log('searchParams is', searchParams)
    if (categoryCodes && !(productType && productType.value)) {
      return message.error('请选择商品类型')
    }
    handleGetList({searchParams: {...searchParams, page: 1, size}})
  }
  // query = debounce(() => {
  //   const {handleGetList, searchParams = {}, size} = this.props
  //   const {categoryCodes = '', productType = ''} = searchParams
  //   console.log('searchParams is', searchParams)
  //   if (categoryCodes && !(productType && productType.value)) {
  //     return message.error('请选择商品类型')
  //   }
  //   handleGetList({searchParams: {...searchParams, page: 1, size}})
  // }, 600)

  // 重置
  // @Debounce(600)
  reset = () => {
    const {update, handleGetList, size} = this.props
    update({searchParams: {}})
    handleGetList({searchParams: {page: 1, size}})
  }
  getProductOfTypes = (type) => {
    let searchdata = {
      searchParams: {
        type
      }
    }
    getData({
      api: API_PRODUCTS_KEYVALUES,
      ...searchdata
    })
      .then((data) => {
        this.setState({
          searchProList: data.result || []
        })
      })
      // 相应的逻辑判断
      .catch((err) => {})
  }

  render() {
    const {
      searchParams: {
        sapCusCode = [],
        productCode1 = [],
        sapSubCusCode = '',
        pickingAreaCode = []
      },
      userLoation,
      modalTuttonLoading
    } = this.props
    const {searchProList = []} = this.state
    // const {Option} = Select

    // const children = []
    // for (let i = 10; i < 36; i++) {
    //   children.push(
    //     <Option key={i.toString(36) + i}>{i.toString(36) + i}</Option>
    //   )
    // }
    return (
      <Form className={style.form}>
        <Row type="flex" justify="start">
          <Col span={6}>
            <CsxSearch
              dateApi={API_SHIPINGZONECONFIG_LIST_CUSTOMER}
              label="客户"
              searchParamsKey={['sapCusCode', 'sapCusName']}
              searchParams={{warehouseCode: userLoation.value}}
              selectItem={{
                data: sapCusCode,
                key: 'sapCusCode',
                valueKey: ['sapCusCode', 'sapCusName']
              }}
              onSelect={(value) => {
                console.log(value, 111)
                this.onInputChange({sapCusCode: value})
              }}
              onChangeSelect={(value) => {
                console.log(value, 222)
                this.onInputChange({sapCusCode: value})
              }}
            />
          </Col>
          <Col span={6}>
            <CsxSearch
              dateApi={API_SHIPINGZONECONFIG_LIST_SUB_CUSTOMER}
              label="子客户"
              searchParamsKey={['sapSubCusCode', 'sapSubCusName']}
              searchParams={{
                sapCusCode: sapCusCode && sapCusCode.value,
                sapCusName: sapCusCode && sapCusCode.children
              }}
              selectItem={{
                data: sapSubCusCode,
                key: 'sapSubCusCode',
                valueKey: ['sapSubCusCode', 'sapSubCusName']
              }}
              onSelect={(value) => {
                console.log(value, 111)
                this.onInputChange({sapSubCusCode: value})
              }}
              onChangeSelect={(value) => {
                console.log(value, 222)
                this.onInputChange({sapSubCusCode: value})
              }}
            />
          </Col>
          <Col span={6}>
            <div className="commonInput">
              <label>发货区域：</label>
              <div className="box">
                <Select
                  mode="tags"
                  style={{width: '100%'}}
                  placeholder="请输入(支持多区域搜索)"
                  value={pickingAreaCode}
                  onChange={(value) => {
                    this.onInputChange({pickingAreaCode: value})
                  }}
                >
                  {[]}
                </Select>
              </div>
            </div>
          </Col>
          {/* <Col span={8}>
            <CsxSelect
              label="库区类型"
              data={[{}]}
              antd={{mode: 'tags'}}
              // dateApi={API_DICTIONARYS}
              // searchParams={{dicTypes: 'DATYPE'}}
              selectItem={{data: [{}], key: 'dicKey', valueKey: 'dicValue'}}
              onChangeSelect={(value) => {
                this.onInputChange({reservoirAreaTypeCode: value})
              }}
            />
          </Col> */}
        </Row>
        <Row>
          <Col span={24}>
            <Row type="flex" justify="end">
              <Button
                type="primary"
                style={{marginRight: 20}}
                loading={modalTuttonLoading}
                onClick={this.query}
              >
                查询
              </Button>
              <Button type="primary" onClick={this.reset}>
                重置
              </Button>
            </Row>
          </Col>
        </Row>
      </Form>
    )
  }
}
export default Form.create()(App)



