import * as actionType from './actionType'
import {getData} from 'util'
import {
  API_SHIPINGZONECONFIG_LIST_PAGE,
  API_SHIPINGZONECONFIG_ID_DELETE,
  API_SHIPINGZONECONFIG_UPDATE,
  API_SHIPINGZONECONFIG_DOWNLOAD_TEMPLATE,
  API_SHIPINGZONECONFIG_DET_SUB_CUSTOMER,
  API_SHIPINGZONECONFIG_EXPORT
} from 'api'
//同步
export const update = (object) => ({
  type: actionType.SHIPINGZONECONFIG,
  data: object
})

//异步

// 获取列表
export const handleGetList = (reqData) => (dispatch) => {
  console.log(reqData, 9999)
  let reqObj = reqData.searchParams
  for (let i in reqObj) {
    if (Object.prototype.toString.call(reqObj[i]) == '[object Object]') {
      reqObj[i] = reqObj[i].value
    }
  }
  let {size, page} = reqData.searchParams
  dispatch({type: actionType.SHIPINGZONECONFIG, data: {tableLoading: true}})
  getData({api: API_SHIPINGZONECONFIG_LIST_PAGE, ...reqData})
    .then((data) => {
      // 相应的逻辑判断
      dispatch({
        type: actionType.SHIPINGZONECONFIG,
        data: {
          size,
          page,
          total: data.result.total,
          tableLoading: false,
          searchResult: data.result.records || []
        }
      })
    })
    .catch((err) => {
      dispatch({
        type: actionType.SHIPINGZONECONFIG_FILE
      })
    })
}

// 删除
export const handleDel = (reqData, callBack) => (dispatch) => {
  getData({api: API_SHIPINGZONECONFIG_ID_DELETE, ...reqData})
    .then((data) => {
      callBack && callBack()
    })
    .catch((err) => {
      dispatch({
        type: actionType.SHIPINGZONECONFIG_FILE
      })
    })
}
// 导出
export const handleExport = (reqData) => (dispatch) => {
  dispatch({
    type: actionType.SHIPINGZONECONFIG,
    data: {buttonExportLoading: true}
  })
  let api = API_SHIPINGZONECONFIG_EXPORT
  let reqObj = reqData.searchParams
  for (let i in reqObj) {
    if (Object.prototype.toString.call(reqObj[i]) == '[object Object]') {
      reqObj[i] = reqObj[i].value
    }
  }
  getData({api, ...reqData})
    .then((data) => {
      // 相应的逻辑判断
      dispatch({
        type: actionType.SHIPINGZONECONFIG,
        data: {
          buttonExportLoading: false
        }
      })
    })
    .catch((err) => {
      dispatch({
        type: actionType.SHIPINGZONECONFIG_FILE
      })
    })
}
// 下载模板
export const handleDownloadTem = (reqData) => (dispatch) => {
  dispatch({
    type: actionType.SHIPINGZONECONFIG,
    data: {buttonExportLoading_template: true}
  })
  let api = API_SHIPINGZONECONFIG_DOWNLOAD_TEMPLATE
  getData({api, ...reqData})
    .then((data) => {
      // 相应的逻辑判断
      dispatch({
        type: actionType.SHIPINGZONECONFIG,
        data: {
          buttonExportLoading_template: false
        }
      })
    })
    .catch((err) => {
      dispatch({
        type: actionType.SHIPINGZONECONFIG_FILE
      })
    })
}

// 获取子客户的发货区域
export const handleGetSubCusInfo = (reqData, callBack) => (dispatch) => {
  let api = API_SHIPINGZONECONFIG_DET_SUB_CUSTOMER
  getData({api, ...reqData})
    .then((data) => {
      // 相应的逻辑判断
      dispatch({
        type: actionType.SHIPINGZONECONFIG,
        data: {
          modalTuttonLoading: false
        }
      })
      callBack && callBack(data)
    })
    .catch((err) => {
      dispatch({
        type: actionType.SHIPINGZONECONFIG_FILE
      })
    })
}
// 编辑发货区域配置
export const handleEdit = (reqData, callBack) => (dispatch) => {
  dispatch({
    type: actionType.SHIPINGZONECONFIG,
    data: {modalTuttonLoading: true}
  })
  let api = API_SHIPINGZONECONFIG_UPDATE
  getData({api, ...reqData})
    .then((data) => {
      // 相应的逻辑判断
      dispatch({
        type: actionType.SHIPINGZONECONFIG,
        data: {
          modalTuttonLoading: false,
          showUpdateModal: false
        }
      })
      callBack && callBack(data)
    })
    .catch((err) => {
      dispatch({
        type: actionType.SHIPINGZONECONFIG_FILE
      })
    })
}



