import React, {PureComponent} from 'react'
import {Table, Modal, message, Radio, Select} from 'antd'
const confirm = Modal.confirm
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'

@connect(
  (state) => ({...state.shipingZoneConfig, userLoation: state.userLoation}),
  mapDispatchToProps
)
export default class app extends PureComponent {
  constructor() {
    super()
    this.state = {}
    this.columns = [
      {
        title: '客户编号',
        width: 100,
        dataIndex: 'sapCusCode',
        key: 'sapCusCode'
      },
      {
        title: '客户名称',
        width: 100,
        dataIndex: 'sapCusName',
        key: 'sapCusName'
      },
      {
        title: '子客户编号',
        width: 120,
        dataIndex: 'sapSubCusCode',
        key: 'sapSubCusCode',
        render: (text, record) => `${text}`
      },
      {
        title: '子客户名称',
        width: 100,
        dataIndex: 'sapSubCusName',
        key: 'sapSubCusName'
      },
      {
        title: '发货区域',
        width: 100,
        dataIndex: 'pickingAreaCode',
        key: 'pickingAreaCode'
      },
      {
        title: '更新人',
        width: 120,
        dataIndex: 'updateBy',
        key: 'updateBy'
      },
      {
        title: '更新时间',
        width: 180,
        dataIndex: 'updateTime',
        key: 'updateTime'
      }
    ]
  }

  componentDidMount() {
    const {handleGetList, size} = this.props
    handleGetList({searchParams: {page: 1, size}})
  }

  // 监听下拉仓库改变了重新获取数据
  componentWillReceiveProps(newProps, newStates) {
    const {handleGetList, userLoation, size} = this.props
    if (newProps.userLoation.value != userLoation.value) {
      handleGetList({searchParams: {page: 1, size}})
    }
  }

  // 排序 columns排序列
  // isAsc 是否升序 true, false
  onChange = (pagination, filters, sorter, extra) => {
    const {handleGetList, searchParams, page, size} = this.props
    if (sorter.order) {
      if (sorter.order === 'ascend') {
        handleGetList({
          searchParams: {
            page,
            size,
            ...searchParams,
            isAsc: true,
            columns: sorter.field
          }
        })
      } else {
        handleGetList({
          searchParams: {
            page,
            size,
            ...searchParams,
            isAsc: false,
            columns: sorter.field
          }
        })
      }
    } else {
      // 判断是点跳页面还是排序
      handleGetList({
        searchParams: {
          page: pagination.current,
          size: pagination.pageSize,
          ...searchParams
        }
      })
    }
  }

  render() {
    const {
      total,
      page,
      size,
      tableLoading,
      searchResult,
      selectedRowKeys = []
    } = this.props
    let pagination = {
      total,
      showTotal: (total, range) => `共 ${total} 条`, // 显示总数
      showQuickJumper: true, // 是否可以快速跳转至某页
      current: page,
      pageSize: size,
      showSizeChanger: true, // 是否可以改变 pageSize
      className: 'pagination',
      pageSizeOptions: ['50', '100', '200', '500']
    }
    let rowSelection = {
      selectedRowKeys,
      onChange: (selectedRowKeys, selectedRows) => {
        const {update} = this.props
        update({selectedRowKeys, selectedRows})
      }
    }
    return (
      <div className="table-group">
        <Table
          rowKey={(record) => record.id}
          loading={tableLoading}
          columns={this.columns}
          dataSource={searchResult}
          scroll={{x: 'max-content', y: 400}}
          onChange={this.onChange}
          pagination={pagination}
          rowSelection={rowSelection}
        />
      </div>
    )
  }
}



