import React, {PureComponent} from 'react'
import {message, Button, Modal, Alert} from 'antd'
import {Import} from 'HOC'
import style from './style/Tool.scss'
import {
  API_WMS_SHIPINGZONECONFIG_IMPORT,
  API_SHIPINGZONECONFIG_EXPORT_ERROR
} from 'api'
import {connect} from 'react-redux'
import {getData} from 'util'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'
import {debounce} from 'lodash'
const confirm = Modal.confirm
@connect((state) => ({...state.shipingZoneConfig}), mapDispatchToProps)
export default class TableTool extends PureComponent {
  constructor() {
    super()
    this.state = {}
  }
  /* 导入成功 */
  handleImportSuccess = (res) => {
    const {update, handleGetList, searchParams, page, size} = this.props
    update({importBtnLoading: false})
    res && res.message === 'ok' && message.success('导入成功')
    handleGetList({searchParams: {page, size, ...searchParams}})
  }
  /* 报错导出 */
  handleExprotError = (data) => {
    console.log('data is', data)
    let urlPath = API_SHIPINGZONECONFIG_EXPORT_ERROR
    getData({
      api: urlPath
    }).then(() => {
      message('导出报错成功')
    })
  }
  //下载错误信息
  DownErrorInfo = () => {}
  //下载模板
  export = () => {
    const {handleDownloadTem, searchParams, page, size} = this.props
    handleDownloadTem({searchParams: {page, size}}, () => {
      message.success('导出成功')
    })
  }

  handleUpdate = () => {
    const {update} = this.props
    update({
      showUpdateModal: true,
      modalData: {}
    })
  }
  //批量删除
  handleDelete = () => {
    const {
      selectedRowKeys,
      handleDel,
      handleGetList,
      searchParams,
      userLoation,
      page,
      size
    } = this.props
    if (selectedRowKeys.length == 0) {
      message.error('请先选择需要删除的发货区域配置')
      return
    }
    console.log(selectedRowKeys.length)
    let reqObj = selectedRowKeys.join(',')
    console.log(reqObj)
    confirm({
      title: '温馨提示',
      content: `确认将这${selectedRowKeys.length}条发货区域配置删除？`,
      okText: '确认',
      width: 520,
      cancelText: '取消',
      onOk() {
        handleDel({data: {ids: reqObj}}, () => {
          message.success('操作成功')
          handleGetList({
            searchParams: {...searchParams, page, size},
            userLoation
          })
        })
      }
    })
  }
  // 导出门店列表
  exportList = debounce(() => {
    const {handleExport, searchParams, userLoation} = this.props
    handleExport(
      {
        searchParams: {
          ...searchParams,
          productName: searchParams.productName
            ? searchParams.productName.children
            : ''
        },
        userLoation
      },
      () => {
        message.success('导出成功')
      }
    )
  }, 300)
  render() {
    const {
      importBtnLoading = false,
      buttonLoading = false,
      buttonExportLoading_template = false,
      buttonExportLoading = false
    } = this.props
    return (
      <div className={style['table-tool']}>
        <Button
          type="primary"
          className={style.btn}
          onClick={this.export}
          loading={buttonLoading}
        >
          下载模板
        </Button>
        <Import
          action={{api: API_WMS_SHIPINGZONECONFIG_IMPORT}}
          onOk={this.handleImportSuccess}
          onExprotError={this.handleExprotError}
          urlToGetErrorList={true}
          beforeImport={() => {
            const {update} = this.props
            update({tableLoading: true, importBtnLoading: true})
          }}
          onResponse={() => {
            const {update} = this.props
            update({tableLoading: false, importBtnLoading: false})
          }}
        >
          <Button
            loading={importBtnLoading}
            type="primary"
            className={style.btn}
          >
            导入更新
          </Button>
        </Import>
        <Button
          type="primary"
          className={style.btn}
          onClick={this.handleUpdate}
        >
          更新
        </Button>
        <Button
          type="primary"
          className={style.btn}
          onClick={this.handleDelete}
        >
          批量删除
        </Button>
        <Button
          type="primary"
          className={style.btn}
          onClick={this.exportList}
          loading={buttonExportLoading}
        >
          导出
        </Button>
        <div style={{width: '300px', display: 'inline-block'}}>
          <Alert
            message="发货区域维护成功后，次日生效"
            type="info"
            showIcon={false}
            banner={true}
            style={{borderRadius: '3px'}}
          />
        </div>
      </div>
    )
  }
}



