import React, {PureComponent} from 'react'
import {Modal, Row, Col, Input, Form, Button, message, InputNumber} from 'antd'
const FormItem = Form.Item
import {
  API_USER_WAREHOUSELIST,
  API_NEWPRODUCTSTOCK_GETPRODUCTKEYVALUES,
  API_SHIPINGZONECONFIG_LIST_SUB_CUSTOMER,
  API_GET_CONFIGURE_CONFIGURE_DEVICE_SAP_SUB_CODE
} from 'api'
import {CsxFormSelect, CsxFormSearch} from 'Form'
import {connect} from 'react-redux'
import {mapDispatchToProps} from '../redux/mapDispatchToProps'

@connect(
  (state) => ({...state.shipingZoneConfig, userLoation: state.userLoation}),
  mapDispatchToProps
)
class App extends PureComponent {
  constructor() {
    super()
    this.state = {}
  }
  componentDidMount() {
    // 数据初始化
    const {modalType, update, userLoation, modalData} = this.props

    console.log(modalData, 333)
    let reqObj = {...modalData}
    reqObj.warehouseCode = {
      value: reqObj.warehouseCode,
      children: reqObj.warehouseName
    }
    update({modalData: {...reqObj}})
  }

  // 关闭弹窗
  handleCloseModal = () => {
    const {update} = this.props
    update({showUpdateModal: false})
  }

  // 改变当前的输入值
  onInputChange(values) {
    const {modalData, update} = this.props
    console.log('values is', values)
    console.log('{...modalData, ...values} is', {...modalData, ...values})
    update({modalData: {...modalData, ...values}})
  }

  // 提交
  handleSubmit = (e) => {
    e.preventDefault()
    const {
      form,
      handleGetList,
      handleEdit,
      modalData,
      modalType,
      userLoation,
      size,
      searchParams
    } = this.props
    form.validateFieldsAndScroll((err, fieldsValue) => {
      if (!err) {
        // return;
        let reqObj = {...modalData}
        //创建

        reqObj.sapSubCusCode =
          reqObj.sapSubCusCode && reqObj.sapSubCusCode.value
        reqObj.warehouseCode = userLoation.value
        delete reqObj.oldSapSubCusCode
        if (reqObj) console.log(reqObj)

        //编辑
        reqObj.id = modalData.id
        handleEdit({data: {...reqObj}}, () => {
          handleGetList({
            searchParams: {
              warehouseCode: searchParams.warehouseCode,
              page: 1,
              size
            }
          })
          message.success('操作成功')
        })
      }
    })
  }

  render() {
    const {
      modalType,
      form: {getFieldDecorator},
      modalTuttonLoading,
      modalData,
      handleGetSubCusInfo
    } = this.props
    console.log(333, modalData)
    const formItemLayout = {
      labelCol: {
        xs: {span: 24},
        sm: {span: 8}
      },
      wrapperCol: {
        xs: {span: 24},
        sm: {span: 10}
      }
    }
    return (
      <Modal
        title={'发货区域更新'}
        visible={true}
        onCancel={this.handleCloseModal}
        mask={false}
        className="operationModal"
        footer={null}
        width="640px"
      >
        <Form onSubmit={this.handleSubmit}>
          <Row>
            <FormItem {...formItemLayout} label="子客户">
              {getFieldDecorator('productCode', {
                initialValue: '',
                rules: [
                  {
                    required: true,
                    message: '请填写!'
                  }
                ]
              })(
                <CsxFormSearch
                  dateApi={API_SHIPINGZONECONFIG_LIST_SUB_CUSTOMER}
                  searchParamsKey={['sapSubCusCode', 'sapSubCusName']}
                  selectItem={{
                    data: modalData.sapSubCusCode,
                    key: 'sapSubCusCode',
                    valueKey: ['sapSubCusCode', 'sapSubCusName']
                  }}
                  onChangeSelect={(value) => {
                    console.log('value is onChangeSelect', value)
                    this.onInputChange({
                      sapSubCusCode: value
                    })
                  }}
                  onSelect={(value) => {
                    const {value: val = ''} = value
                    this.onInputChange({sapSubCusCode: value})
                    handleGetSubCusInfo(
                      {
                        searchParams: {
                          sapSubCusCode: val
                        }
                      },
                      (data) => {
                        const {result = {}} = data
                        this.onInputChange({
                          oldSapSubCusCode:
                            (result && result.pickingAreaCode) || ''
                        })
                      }
                    )
                  }}
                />
              )}
            </FormItem>
            <Col span={24}>
              <FormItem {...formItemLayout} label={'原发货区域'}>
                {getFieldDecorator('oldSapSubCusCode', {
                  initialValue: modalData.oldSapSubCusCode
                })(<Input disabled={true} />)}
              </FormItem>
            </Col>
            <Col span={24}>
              <FormItem {...formItemLayout} label={'更新发货区域'}>
                {getFieldDecorator('pickingAreaCode', {
                  initialValue: modalData.pickingAreaCode,
                  rules: [
                    {
                      required: true,
                      message: '请填写!'
                    },
                    {
                      // pattern: /^([1-9][0-9]*)$/,
                      pattern: /^[0-9]\d{0,5}$/,
                      message: '请输入不超过6位的正整数'
                    }
                  ]
                })(
                  <Input
                    placeholder={'请输入不超过6位整数'}
                    onChange={(e) =>
                      this.onInputChange({pickingAreaCode: e.target.value})
                    }
                  />
                )}
              </FormItem>
            </Col>
          </Row>
          <Row type="flex" justify="center">
            <Col>
              <Button
                style={{marginRight: '48px'}}
                onClick={this.handleCloseModal}
              >
                取消
              </Button>
              <Button
                type="primary"
                htmlType="submit"
                loading={modalTuttonLoading}
              >
                保存
              </Button>
            </Col>
          </Row>
        </Form>
      </Modal>
    )
  }
}
export default Form.create()(App)



